<template>
  <div class="backgd">
    <!-- v-for循环出菜品 -->
    <div class="tables">
      <table border="0" cellspacing="0">
        <thead>
          <tr>
            <th>勾选</th>
            <th>图片</th>
            <th>菜名</th>
            <th>单价</th>
            <th>购买数量</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(pron, index) in menulist" :key="index" class="dishtable">
            <td><input type="checkbox" /></td>
            <td><img :src="pron.icon" class="picon" /></td>
            <td>{{ pron.name }}</td>
            <td>￥{{ pron.price }}</td>
            <td>
              <button
                :disabled="pron.num === 0"
                @click="pron.num--"
                class="add"
              >
                -
              </button>
              {{ pron.num }}
              <button @click="pron.num++" class="jian">+</button>
            </td>
            <td>
              <button @click="deleteBook(index)">删除</button>
            </td>
          </tr>
        </tbody>
      </table>
